.App {
  text-align: center;
  background-color: 'yellow';
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.home{

  height:880px;
  background-color: #16304d;
  text-align: center;
}

.ul{
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.topnav{
  margin:0px;
  height: 90px;
  background-color: #a10647;
  align-items: center;
  justify-content: space-around;
  box-shadow: 0px 25px 8px -6px rgba(0,0,0,.5);
}